.post__article {
    text-align: center;
    overflow: hidden;
    margin-bottom: 8px;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    border: none;
    border-radius: 0 0 0 1px rgb(0 0 0 / 15%), 0 0 0 rgb(0 0 0 / 20%);
    padding: 0;
    margin: 0 0 8px;
    overflow: visible;
}

.post__sharedActor {
    padding-right: 40px;
    flex-wrap: nowrap;
    padding: 12px 16px 0;
    margin-bottom: 8px;
    align-items: center;
    display: flex;
}
.post__sharedActor > a {
    margin-right: 12px;
    flex-grow: 1;
    overflow: hidden;
    display: flex;
    text-decoration: none;
}
.post__sharedActor > img {
    width: 48px;
    height: 48px;
}
.post__sharedActor > div {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 0;
    margin-left: 8px;
    overflow: hidden;
}
.post__sharedActor > div > span {
    text-align: left;
}
.post__sharedActor > div > span > first-child {
    font-weight: 700;
                    color: rgba(0, 0, 0, 1);
                    color: rgba(0,0,0,0.87); font-family: Roboto; font-size: 20px; font-weight: 500; letter-spacing: 0.15px; line-height: 24px;
}
.post__sharedActor > button {
    position: absolute;
    right: 12px;
    outline: none;
    border: none;
    top: 0;
    background: transparent;
}
.post__description {
    padding: 0 16px;
    overflow: hidden;
    color: rgba(0,0,0,0.9);
    font-size: 14px;
    text-align: left;
    color: rgba(0,0,0,0.87); font-family: Roboto; font-size: 16px; letter-spacing: 0.5px; line-height: 24px;
}
.post__sharedImage {
    margin-top: 8px;
    width: 100%;
    min-height: 250px;
    display: block;
    position: relative;
    background-color: #f9fafb;
}
.post__sharedImage > img {
    object-fit: fill;
    width: 100%;
    height: 100%;
}
.post__socialAction {
    align-items: center;
    display: flex;
    justify-content: flex-start;
    margin: 0;
    min-height: 40px;
    padding: 4px 8px;
}
.post__socialAction > button {
    display: inline-flex;
        align-items: center;
        padding: 8px;
        color: #0a66c2;
        border: none;
        cursor: pointer;
        background-color: #fff;
        
}
.post__socialAction > p {
    color: #0a66c2;
    margin: 0 10px 0 10px;
}
@media (min-width: 768px) {
    span {
        margin-left: 8px;
    }
}

